	module.exports = {"data":{"nodePost":{"title":"My Brief Unretirement From Comedy","body":{"value":"<p>Hard to believe, but it has been years since I've been on stage doing anything resembling improv. I would have guessed that my comedy days were over for good. Well, they still are! But for one night I'm getting back on stage with some old friends at my old stomping grounds to improvise like it was 2008. Will we change the face of comedy forever?  Will we mostly talk about our kids while hanging out in the green room? Will we even be able to stay awake past 10:30 PM? <a href=\"https://www.eventbrite.com/e/hnx-harold-night-celebrates-ten-years-tickets-45323998299\">Join us</a> on Wednesday May 2, 2018 to find out.</p>"},"relationships":{"image":{"localFile":{"childImageSharp":{"sizes":{"base64":"data:image/jpeg;base64,","aspectRatio":2,"src":"/static/55bca5feb31d2775e0ed476fc258732b-f8e23cb44d8695c68dd36faa722b3dbb-9332f.jpg","srcSet":"/static/55bca5feb31d2775e0ed476fc258732b-f8e23cb44d8695c68dd36faa722b3dbb-5ea23.jpg 313w,\n/static/55bca5feb31d2775e0ed476fc258732b-f8e23cb44d8695c68dd36faa722b3dbb-8f7c3.jpg 625w,\n/static/55bca5feb31d2775e0ed476fc258732b-f8e23cb44d8695c68dd36faa722b3dbb-9332f.jpg 800w","sizes":"(max-width: 800px) 100vw, 800px"}}}}},"created":1525194488}},"pathContext":{"slug":"/post/my-brief-unretirement-from-comedy/"}}

